.projects{
    background-color: #5732a8;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    .arrow{
        height: 50px;
        position: absolute;
        cursor: pointer;
        &.left{
            left: 150px;
            transform: rotateY(180deg);
        }

        &.right{
            right: 150px;
        }
    }
    .slider{
        height: 350px;
        display: flex;
        position: absolute;
        left:0;
        transition: all 1s ease-out;

        .container{
            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: center;

            .item{
                width: 700px;
                height: 100%;
                background-color: white;
                border-radius: 20px;
                display: flex;
                align-items: center;
                justify-content: center;

                .left{
                    flex:4;
                    height: 80%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                
                    .leftContainer{
                        width:90%;
                        height: 70%;
                        display:flex;
                        flex-direction: column;
                        justify-content: space-between;
                        
                        .imgContainter{
                            width: 40px;
                            height: 40px;
                            border-radius: 50%;
                            background-color: coral;
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            img{
                                width: 25px;
                            }
                        }
                        h2{
                            font-size: 20px;
                        }
                        p{
                            font-size: 14px;
                        }
                        span{
                            font-size: 15px;
                            font-weight: 600;
                            text-decoration: underline;
                            cursor: pointer;
                        }
                    }
                }
                .right{
                    flex:8;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    overflow: hidden;
                    img{
                        width: 400px;
                        transform: rotate(-10deg);
                        overflow: hidden;
                    }
                }
            }
        }
    }
}